<template>
  <div :class="mobile === true ? 'container navbar ' : 'navbar'">
    <div
      style="color: #77a9f9"
      class="navtext"
      @click="
        ChangeActivePage('PeopleAndApartments');
        UpdateServiceInfo();
      "
    >
      Справочники
    </div>
    <div
      style="color: #77a9f9"
      class="navtext"
      @click="
        ChangeActivePage('ServiceInfoAdmin');
        GetAllLodgers();
      "
    >
      Выставить счёт
    </div>
    <div
      style="color: #77a9f9"
      class="navtext"
      @click="
        ChangeActivePage('DrawIndicationsAdmin');
        GetAllLodgers();
        UpdateIndicationsInfo();
      "
    >
      Показания
    </div>
    <a style="color: #77a9f9" download href="http://127.0.0.1:5000/get_excel/"
      >Скачать Exel таблицу</a
    >
    <button
      class="btn"
      @click="
        SignOut();
        this.$cookies.set('token', '');
        this.$cookies.set('id', '');
      "
    >
      Выйти
    </button>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["active"]),
    mobile() {
      return window.innerWidth > window.innerHeight;
    },
  },

  methods: {
    ...mapActions([
      "UpdateUserInfo",
      "UpdateServiceInfo",
      "UpdateIndicationsInfo",
      "GetAllLodgers",
    ]),

    ...mapMutations(["ChangeActivePage", "SignOut"]),
  },

  data() {
    return {};
  },
  components: {},
};
</script>

